import React, { useState, useEffect } from "react";
import Banning from "./ChildViewBan.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import { supabase } from "./createClient";
import {Link} from 'react-router-dom';
import { useAuth } from "./useAuth";

function ParentViewBan() {
  const [SITE, setSITE] = useState([]);
  const [searchQuery, setSearchQuery] = useState(""); // State to store the search input
  const id = 1;

	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};

  // Fetch all sites initially
  useEffect(() => {
    fetchSITE();
  }, []);

  async function fetchSITE() {
    const { data } = await supabase.from("BANNED_WEBSITE").select("*").eq("Parent", id);
    setSITE(data);
    console.log(data);
  }

  useEffect(() => {
    if (searchQuery === "") {
      fetchSITE();
    } else {
      search();
    }
  }, [searchQuery]);

  async function search() {
    const { data } = await supabase.from("BANNED_WEBSITE").select("*").ilike("WebsiteURL", `%${searchQuery}%`);
    setSITE(data);
    console.log(data);
  }

  const handleInputChange = (event) => {
    setSearchQuery(event.target.value);
  };

  return (
    <div>
      <header className={Banning.header}>
        <div className={Banning.logocontainer}>
          <Link className={Banning.Link} to='/ChildDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={Banning.Link} to='/ChildDashboard'><div className={Banning.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={Banning.nav}>
          <ul className={Banning.pad}>
            <li>
              <Link to='/InformationPage'>Educational Materials</Link>
            </li>
            <li>
              <Link to='/Plugin'>Plugin</Link>
            </li>
            <li>
              <Link to='/ReportSystem'>Reporting System</Link>
            </li>
            <li>
              <button className={Banning.buttons} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>
      <div className={Banning.unban}>
        <h1>Banned Websites</h1>
        <form>
          <label className={Banning.label} htmlFor="web">
            Enter URL to know if website is banned<br />
          </label>
          <input
            type="text"
            id="web"
            name="web"
            value={searchQuery}
            onChange={handleInputChange}
            required
          />
          <br />
        </form>
        <div className={Banning.unbanlist}>
          {SITE.map((site) => (
            <div className={Banning.unbanline} key={site.BanID}>
              <p>
                {site.WebsiteURL}
              </p>
            </div>
          ))}
        </div>
      </div>
      <footer className={Banning.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={Banning.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
}

export default ParentViewBan;
