import React, { useState } from "react";
import LoginCSS from "./LoginPage.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import { Link } from "react-router-dom";
import { supabase } from "./createClient";

const Login = () => {
  const handleSubmit = async (e) => {
    e.preventDefault();

    const email = e.target.username.value.trim();
    const password = e.target.password.value.trim();

    try {
      const { data: childData } = await supabase
        .from("CHILD")
        .select("ChildEmail, ChildPass")
        .eq("ChildEmail", email)
        .single();

      if (childData && childData.ChildPass === password) {
        localStorage.setItem("user", JSON.stringify({ email, accountType: "Child" }));
        alert("Welcome, Child!");
        window.location.href = "/ChildDashboard";
        return;
      }

      const { data: parentData } = await supabase
        .from("PARENT")
        .select("ParentEmail, ParentPass")
        .eq("ParentEmail", email)
        .single();

      if (parentData && parentData.ParentPass === password) {
        localStorage.setItem("user", JSON.stringify({ email, accountType: "Parent" }));
        alert("Welcome, Parent!");
        window.location.href = "/ParentDashboard";
        return;
      }

      const { data: raData } = await supabase
        .from("RELEVANT_AUTHORITY")
        .select("RAemail, RAPass")
        .eq("RAemail", email)
        .single();

      if (raData && raData.RAPass === password) {
        localStorage.setItem("user", JSON.stringify({ email, accountType: "Relevant Authority" }));
        alert("Welcome, Relevant Authority!");
        window.location.href = "/RelevantAuthority";
        return;
      }

      alert("No account found for the provided credentials.");
    } catch (err) {
      console.error("Unexpected error during login:", err);
      alert("An unexpected error occurred. Please try again.");
    }
  };

  return (
    <div>
      <header className={LoginCSS.header}>
        <div className={LoginCSS.logocontainer}>
          <Link to="/">
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: '50px', height: '50px' }}
            />
          </Link>
          <Link className={LoginCSS.Link} to="/"><div className={LoginCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={LoginCSS.nav}>
          <ul className={LoginCSS.pad}>
            <li>
              <Link to="/ReportSystem">Reporting System</Link>
            </li>
            <li>
              <button className={LoginCSS.buttonsa} onClick={() => (window.location.href = '/Login')}>Login</button>
            </li>
          </ul>
        </nav>
      </header>

      <div className={LoginCSS.body}>
        <div className={LoginCSS.container}>
          <div className={LoginCSS.loginleft}>
            <h1>Log In</h1>
            <p>Enter Your Credentials</p>
          </div>

          <div className={LoginCSS.loginright}>
            <form className={LoginCSS.form} onSubmit={handleSubmit}>
              <label htmlFor="username">Email</label>
              <input className={LoginCSS.input} type="text" id="username" placeholder="Enter your Email" />

              <label htmlFor="password">Password</label>
              <input className={LoginCSS.input}
                type="password"
                id="password"
                placeholder="Enter your password"
              />

              <div className={LoginCSS.buttons}>
                <Link className={LoginCSS.Link} to='/SignUp'><button type="button" className={LoginCSS.cancelbtn}>
                  Sign Up
                </button></Link>
                <button type="submit" className={LoginCSS.loginbtn}>
                  Log In
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>

      <footer className={LoginCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={LoginCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default Login;
