import { useNavigate } from "react-router-dom";
import { useEffect } from "react";

export const useAuth = () => {
  const navigate = useNavigate();

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (!user) {
      alert("Please log in to access this page.");
      navigate("/LogIn");
    }
  }, [navigate]);
};
