import React,{ useState, useEffect } from "react";
import PlugCSS from "./PlugIn.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import kid from './Pictures/kidshield.png';
import install from './Pictures/instructions.png';
import JSZip from 'jszip';
import { Link, useNavigate } from 'react-router-dom';
import { saveAs } from 'file-saver';
import { useAuth } from "./useAuth";

const PlugIn = () => {
  const Parent = 2;
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const navigate = useNavigate();
  const [userAccountType, setUserAccountType] = useState(null);
  
  useAuth();
  
  useEffect(() => {
	  const user = JSON.parse(localStorage.getItem("user"));
	  if (user) {
		setIsLoggedIn(true);
		setUserAccountType(user.accountType); // Save the account type in state
	  } else {
		setIsLoggedIn(false);
	  }
	}, []);
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};
  
  const downloadExtension = async () => {
    const zip = new JSZip();
  
    const backgroundJsContent = `
      const SUPABASE_URL = "https://zsmlkwmdshusflosrcjv.supabase.co";
      const SUPABASE_KEY = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InpzbWxrd21kc2h1c2Zsb3NyY2p2Iiwicm9sZSI6InNlcnZpY2Vfcm9sZSIsImlhdCI6MTczMjAwNDUyOSwiZXhwIjoyMDQ3NTgwNTI5fQ.IKnhV3C3k5nz9OTB1OCl8ZIgZfSQvhpnpuaj4v5CNVc"; // Replace with your actual API key
  
      async function fetchBlockedWebsites() {
        try {
          // Fetch data from the Supabase REST API with a filter on the Parent column
          const response = await fetch(\`\${SUPABASE_URL}/rest/v1/BANNED_WEBSITE?Parent=eq.PARENT_VALUE\`, {
            method: "GET",
            headers: {
              "apikey": SUPABASE_KEY,
              "Authorization": \`Bearer \${SUPABASE_KEY}\`,
              "Content-Type": "application/json"
            }
          });
  
          if (!response.ok) {
            console.error("Failed to fetch websites from Supabase:", response.statusText);
            return;
          }
  
          const websites = await response.json();
          const urls = websites.map(site => site.WebsiteURL.toLowerCase()); // Normalize to lowercase
  
          chrome.storage.local.set({ blockedWebsites: urls }, () => {
            console.log("Blocked websites updated:", urls);
          });
        } catch (error) {
          console.error("Error fetching websites from Supabase:", error);
        }
      }
  
      function checkAndBlockWebsite(details) {
        try {
          const url = new URL(details.url); // Parse the URL
          const currentHost = url.hostname.toLowerCase(); // Normalize to lowercase
  
          chrome.storage.local.get("blockedWebsites", data => {
            const blockedWebsites = data.blockedWebsites || [];
            console.log("Checking if URL is blocked:", currentHost);
  
            if (blockedWebsites.some(website => currentHost.includes(website))) {
              console.log(\`Blocked: \${currentHost}\`);
              chrome.tabs.update(details.tabId, { url: "about:blank" });
            }
          });
        } catch (error) {
          console.error("Error processing navigation event:", error);
        }
      }
  
      chrome.webNavigation.onBeforeNavigate.addListener(checkAndBlockWebsite);
  
      chrome.runtime.onInstalled.addListener(() => {
        console.log("Website Blocker installed or updated");
        fetchBlockedWebsites();
      });
  
      setInterval(fetchBlockedWebsites, 5 * 60 * 1000); // 5 minutes
    `;
  
    const modifiedBackgroundJsContent = backgroundJsContent.replace('PARENT_VALUE', Parent);
  
    const manifestContent = `
      {
        "manifest_version": 3,
        "name": "Website Blocker",
        "version": "1.0",
        "description": "Blocks websites based on a Supabase list",
        "permissions": [
          "storage",
          "webNavigation",
          "tabs"
        ],
        "host_permissions": [
          "<all_urls>"
        ],
        "background": {
          "service_worker": "background.js",
          "type": "module"
        }
      }
    `;
    
    zip.file("extension/manifest.json", manifestContent);
    zip.file("extension/background.js", modifiedBackgroundJsContent);
  
    const contentScriptContent = `console.log('Content script running');`;
    zip.file("extension/content.js", contentScriptContent);
  
    const zipBlob = await zip.generateAsync({ type: "blob" });
  
    saveAs(zipBlob, "website-blocker-extension.zip");
  };
  

  return (
    <div>
      <header className={PlugCSS.header}>
        <div className={PlugCSS.logocontainer}>
          <Link to={isLoggedIn ? userAccountType == "Parent" ? "/ParentDashboard" : userAccountType == "Child" ? "/ChildDashboard" : "/" : "/"}>
            <img src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}/>
          </Link>
          <Link className={PlugCSS.Link} to={isLoggedIn ? userAccountType == "Parent" ? "/ParentDashboard" : userAccountType == "Child" ? "/ChildDashboard" : "/" : "/"}>
		   <div className={PlugCSS.logotext}>ChildWatch Secure</div>
          </Link>
        </div>
        <nav className={PlugCSS.nav}>
          <ul className={PlugCSS.pad}>
			{userAccountType === "Child" && (
					<Link className={PlugCSS.Link} to="/InformationPage">
					  <span className={PlugCSS.pluginText}>Educational Materials</span>
					</Link>)}
            <li><Link className={PlugCSS.Link} to='/Plugin'>Plugin</Link></li>
			<li><Link className={PlugCSS.Link} to='/ReportSystem'>Reporting System</Link></li>
            <button className={PlugCSS.buttons} onClick={handleLogout}>Logout</button>
          </ul>
        </nav>
      </header>

      <main>
        <section className={PlugCSS.mainbanner}>
          <div className={PlugCSS.bannercontent}>
            <img src={kid} alt="ChildWatch Shield logo" className={PlugCSS.shieldlogo} />
            <div>
              <h3>ChildWatch Secure - Protecting Children Online</h3>
              <p>A trusted resource for protecting children from online harm.</p>
            </div>
          </div>
          <button className={PlugCSS.topbutton}>Learn More</button>
        </section>

        <section className={PlugCSS.pluginsection}>
          <div>
            <h2>ChildSafe Secure Plugin</h2>
            <p>Protect your child from inappropriate online content with our plugin.</p>
            <button className={PlugCSS.topbutton} onClick={downloadExtension}>
              Download Now
            </button>
          </div>
          <img src={kid} alt="Secure Plugin Image" className={PlugCSS.pluginimage} />
        </section>

        <section className={PlugCSS.installationsection}>
          <img src={install} alt="Installation Image" className={PlugCSS.installimage} />
          <div className={PlugCSS.installinstructions}>
            <h2>Installing ChildWatch Secure Plugin</h2>
            <p>Follow these simple steps to install and use the plugin.</p>
            <div className={PlugCSS.step}><h4>Step 1:</h4><p>Click "Download Plugin".</p></div>
            <div className={PlugCSS.step}><h4>Step 2:</h4><p>Extract the zipped folder and remember where you extracted it.</p></div>
            <div className={PlugCSS.step}><h4>Step 3:</h4><p>Go to google chrome and search for chrome://extensions/</p></div>
            <div className={PlugCSS.step}><h4>Step 4:</h4><p>Activate developer mode which is located on the top right</p></div>
            <div className={PlugCSS.step}><h4>Step 5:</h4><p>Once Developer mode is activated, click on the load unpacked button on the top left</p></div>
            <div className={PlugCSS.step}><h4>Step 6:</h4><p>Search and select the extracted folder from the zipped folder</p></div>
            <div className={PlugCSS.step}><h4>Step 7:</h4><p>Test by going to a banned website</p></div>
            <div className={PlugCSS.step}><h4>Step 8:</h4><p>If successfully blocked, turn off Developer mode and the web blocker is now active and working</p></div>

          </div>
        </section>
      </main>

      <footer className={PlugCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={PlugCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default PlugIn;
