import React, { useState } from 'react';
import AddCSS from './Add.module.css';
import logo from './Pictures/output-onlinepngtools.png';
import { useAuth } from "./useAuth";
import { Link } from 'react-router-dom';
import { supabase } from './createClient';  // Make sure you have supabase initialized here

const AddDesignatedChild = () => {
  useAuth();

  // State for form data
  const [formData, setFormData] = useState({
    name: '',
    birthdate: '',
    age: '',
    gender: 'male',
    username: '',
    password: '',
    confirmPassword: ''
  });

  // Get the ParentID from the stored user data (assuming it's in localStorage)
  const parentID = 1;

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (formData.password !== formData.confirmPassword) {
      alert("Passwords do not match!");
      return;
    }

    try {
      // Fetch the max childID from the CHILD table
      const getMaxId = async (table, column) => {
        const { data, error } = await supabase
          .from(table)            // Specify the table
          .select(column)         // Select the correct column (e.g., 'ParentID' for 'PARENT')
          .order(column, { ascending: false }) // Order by the column in descending order
          .limit(1);              // Limit the result to just the highest (first) row
      
        if (error) throw error;
      
        return data.length > 0 ? data[0][column] : 0;  // Return the value of the specified column, or 0 if no data
      };

      // Check the current max ID for both tables
      const newChildID = await getMaxId('CHILD', 'ChildID');
      // Increment childID by 1 for the new child
      const insertData = {
        ChildID: newChildID + 1,
        ChildName: formData.name,          // The child's name
        ChildGender: formData.gender,      // Gender (male or female)
        ChildBDate: formData.birthdate,    // Birthdate
        Age: formData.age,                 // Age (ensure it's a valid number)
        ChildPass: formData.password,      // The password (you may want to hash it in production)
        ParentID: parentID,                // Parent's ID (stored in the local storage)
        ChildEmail: formData.username      // Username, assuming this is used for email
      };

      // Insert the new child record into the CHILD table
      const { data, error: insertError } = await supabase
        .from("CHILD")
        .insert([insertData]);

        if (insertError) throw insertError;
      alert("Child account created successfully!");
      // Redirect or clear the form after successful creation
      setFormData({
        name: '',
        birthdate: '',
        age: '',
        gender: 'male',
        username: '',
        password: '',
        confirmPassword: ''
      });
    } catch (error) {
      console.error("Error creating child account:", error);
      alert("There was an error creating the account. Please try again.");
    }
  };

  const handleLogout = () => {
    localStorage.removeItem("user");
    alert("You have been logged out.");
    window.location.href = "/";
  };

  return (
    <div>
      <header className={AddCSS.header}>
        <div className={AddCSS.logocontainer}>
          <Link className={AddCSS.Link} to='/ParentDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={AddCSS.Link} to='/ParentDashboard'><div className={AddCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={AddCSS.nav}>
          <ul className={AddCSS.pad}>
            <li>
              <Link to='/Plugin'>Plugin</Link>
            </li>
            <li>
              <Link to='/ReportSystem'>Reporting System</Link>
            </li>
            <li>
              <button className={AddCSS.buttonse} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>

      <main>
        <table className={`${AddCSS.spacetop} ${AddCSS.table}`}>
          <tbody>
            <tr>
              <td className={AddCSS.td} colSpan="2">
                <div className={AddCSS.addaccountcontainer}>
                  <div className={AddCSS.accountinfo}>
                    <div className={AddCSS.text}>
                      <h1>Add A Child Account</h1>
                    </div>
                  </div>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
        <br />
        <br />
        <br />
        <table className={`${AddCSS.spacetop} ${AddCSS.table}`}>
          <tbody>
            <tr>
              <td className={AddCSS.tableText}>
                <h1>Child Account Information</h1>
                <p className={AddCSS.normaltext}>Fill in the Details Below</p>
              </td>
              <td></td>
              <td>
                <div className={AddCSS.formcontainer}>
                  <form className={AddCSS.form} onSubmit={handleSubmit}>
                    <label className={AddCSS.label} htmlFor="name">Name</label>
                    <input
                      type="text"
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      placeholder="Enter your name"
                      className={AddCSS.input}
                    />

                    <label className={AddCSS.label} htmlFor="birthdate">Birthdate</label>
                    <input
                      type="date"
                      id="birthdate"
                      name="birthdate"
                      value={formData.birthdate}
                      onChange={handleChange}
                      className={AddCSS.input}
                    />

                    <label className={AddCSS.label} htmlFor="age">Age</label>
                    <input
                      type="text"
                      id="age"
                      name="age"
                      value={formData.age}
                      onChange={handleChange}
                      placeholder="Enter your age"
                      className={AddCSS.input}
                    />

                    <label>Gender</label>
                    <div className={AddCSS.genderselection}>
                      <div className={AddCSS.radiooptions}>
                        <label className={AddCSS.radiooption}>
                          <input
                            className={AddCSS.input}
                            type="radio"
                            name="gender"
                            value="male"
                            checked={formData.gender === "male"}
                            onChange={handleChange}
                          />
                          <span>Male</span>
                        </label>
                        <label className={AddCSS.radiooption}>
                          <input
                            className={AddCSS.input}
                            type="radio"
                            name="gender"
                            value="female"
                            checked={formData.gender === "female"}
                            onChange={handleChange}
                          />
                          <span>Female</span>
                        </label>
                      </div>
                    </div>

                    <label className={AddCSS.label} htmlFor="username">Email</label>
                    <input
                      className={AddCSS.input}
                      type="text"
                      id="username"
                      name="username"
                      value={formData.username}
                      onChange={handleChange}
                      placeholder="Enter your email"
                    />

                    <label className={AddCSS.label} htmlFor="password">Password</label>
                    <input
                      className={AddCSS.input}
                      type="password"
                      id="password"
                      name="password"
                      value={formData.password}
                      onChange={handleChange}
                      placeholder="Enter your password"
                    />

                    <label className={AddCSS.label} htmlFor="confirmPassword">Confirm Password</label>
                    <input
                      className={AddCSS.input}
                      type="password"
                      id="confirmPassword"
                      name="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      placeholder="Confirm your password"
                    />

                    <div className={AddCSS.buttons}>
                      <button className={AddCSS.button} type="button">Cancel</button>
                      <button className={AddCSS.button} type="submit">Create Account</button>
                    </div>
                  </form>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </main>
      <br />
      <br />
      <br />
    </div>
  );
};

export default AddDesignatedChild;