import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import ReportDets from "./ReportDetails.module.css";
import logo from "./Pictures/output-onlinepngtools.png";
import { supabase } from "./createClient";
import { useAuth } from "./useAuth";
import {Link} from 'react-router-dom';

function ReportDetails() {
	
	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};
	
  const { reportId } = useParams();
  const [details, setDetails] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [status, setStatus] = useState("");

  useEffect(() => {
    fetchReportDetails();
  }, [reportId]);

  async function fetchReportDetails() {
    try {
      const { data, error } = await supabase
        .from("REPORT")
        .select("*")
        .eq("ReportID", reportId)
        .single();

      if (error) {
        throw error;
      }

      setDetails(data);
      setStatus(data.Status);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }

  async function handleDownloadFile(bucketName, filePath) {
    try {
      const { data, error } = await supabase.storage
        .from(bucketName)
        .download(filePath);

      if (error) {
        throw error;
      }

      // Create a Blob URL for the downloaded file
      const blobUrl = URL.createObjectURL(data);

      // Trigger file download
      const anchor = document.createElement("a");
      anchor.href = blobUrl;
      anchor.download = filePath.split("/").pop(); // Use the file name
      anchor.click();

      // Revoke Blob URL after download
      URL.revokeObjectURL(blobUrl);
    } catch (err) {
      alert("Failed to download file: " + err.message);
    }
  }

  const handleStatusChange = async (event) => {
    const newStatus = event.target.value;

    const confirmation = window.confirm(
      "Are you sure you want to update the status?"
    );
    if (confirmation) {
      try {
        const { error } = await supabase
          .from("REPORT")
          .update({
            Status: newStatus,
            StatusChangeDate: new Date().toISOString(),
          })
          .eq("ReportID", reportId);

        if (error) {
          throw error;
        }

        setStatus(newStatus);
        alert("Status updated successfully");
      } catch (err) {
        alert("Failed to update status: " + err.message);
      }
    } else {
      event.target.value = status;
    }
  };

  // Function to format the date (only date, no time)
  const formatDate = (dateString) => {
    if (!dateString) return "N/A";
    const date = new Date(dateString);
    const options = { month: "long", day: "numeric", year: "numeric" };
    return date.toLocaleDateString("en-US", options); // Only return the date (e.g., November 26, 2024)
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  if (!details) {
    return <div>No details found</div>;
  }

  return (
    <div>
      <header className={ReportDets.header}>
        <div className={ReportDets.logocontainer}>
          <Link className={ReportDets.Link} to='/RelevantAuthority'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={ReportDets.Link} to='/RelevantAuthority'><div className={ReportDets.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={ReportDets.nav}>
          <ul className={ReportDets.pad}>
            <li>
              <button className={ReportDets.buttons} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>

      <br />
      <br />
      <main className={ReportDets.unban}>
        <h1>Report Details for ReportID: {details.ReportID}</h1>
        <div className={ReportDets.unbanlist}>
          <table>
            <tbody>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Location</h3>
                </td>
                <td>{details.Location || "N/A"}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Description</h3>
                </td>
                <td>{details.ReportDesc || "N/A"}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Contact Number Provided</h3>
                </td>
                <td>{details.Contact || "N/A"}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Date of Report</h3>
                </td>
                <td>{formatDate(details.DateReported)}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Current Status of Report</h3>
                </td>
                <td>
                  <select className={ReportDets.selects} value={status} onChange={handleStatusChange}>
                    <option value="Pending">Pending</option>
                    <option value="Resolved">Resolved</option>
                    <option value="Closed">Closed</option>
                  </select>
                </td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Date of Status Update</h3>
                </td>
                <td>{formatDate(details.StatusChangeDate)}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Relevant Authority</h3>
                </td>
                <td>{details.RAID || "N/A"}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Department</h3>
                </td>
                <td>{details.Department || "N/A"}</td>
              </tr>
              <tr>
                <td className={ReportDets.attribute}>
                  <h3>Report File</h3>
                </td>
                <td>
                  {details.ReportFile ? (
                    <button className={ReportDets.button}
                      onClick={() =>
                        handleDownloadFile("pdf-reports", details.ReportFile)
                      }
                    >
                      Download {details.ReportFile.split("/").pop()}
                    </button>
                  ) : (
                    "No file attached"
                  )}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </main>

      <footer className={ReportDets.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={ReportDets.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
}

export default ReportDetails;