import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom"; // Import useNavigate from react-router-dom
import REVDASHCSS from "./RelevantAuthority.module.css";
import logo from "./Pictures/output-onlinepngtools.png";
import { supabase } from "./createClient";
import { useAuth } from "./useAuth";
import {Link} from 'react-router-dom';

function RelevantAuthority() {
  const [report, setReport] = useState([]); // State to hold the report data
  const [statusFilter, setStatusFilter] = useState("All"); // State to track the selected status filter
  const [searchTerm, setSearchTerm] = useState(""); // State to track the search term for report ID or description
  const navigate = useNavigate(); // For navigation
  
  useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};

  useEffect(() => {
    fetchReports();
  }, [statusFilter, searchTerm]); // Fetch reports whenever the statusFilter or searchTerm changes

  // Function to fetch reports based on the selected status filter and search term
  async function fetchReports() {
    try {
      let query = supabase.from("REPORT").select("*"); // Start by selecting all reports

      // Apply filter if status is not "All"
      if (statusFilter !== "All") {
        query = query.eq("Status", statusFilter);
      }

      // Apply search filter for report ID or description if a search term exists
      if (searchTerm) {
        query = query.or(
          `ReportDesc.ilike.%${searchTerm}%`
        );
      }

      const { data, error } = await query;

      if (error) {
        console.error("Error fetching reports:", error);
        return;
      }

      setReport(data); // Set the fetched reports to state
    } catch (err) {
      console.error("Error fetching reports:", err);
    }
  }

  // Function to handle status filter change
  const handleStatusChange = (event) => {
    setStatusFilter(event.target.value);
  };

  // Function to handle search term change
  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  // Function to handle row click
  const handleRowClick = (reportID) => {
    navigate(`/report/${reportID}`);
  };

  return (
    <div>
      <header className={REVDASHCSS.repheader}>
        <div className={REVDASHCSS.replogo}>
          <Link className={REVDASHCSS.Link} to='/RelevantAuthority'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={REVDASHCSS.Link} to='/RelevantAuthority'><div className={REVDASHCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={REVDASHCSS.nav}>
          <ul className={REVDASHCSS.pad}>
            <li>
              <button className={REVDASHCSS.buttons} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>

      <br />
      <br />
      <main className={REVDASHCSS.repcontainer}>
        <h1>Anonymous Reports</h1>

        <form className={REVDASHCSS.form} onSubmit={(e) => e.preventDefault()}>
          <label className={REVDASHCSS.label} htmlFor="search">
            Enter ID or Description of Report
            <br />
          </label>
          <input
            type="text"
            name="search"
            value={searchTerm}
            onChange={handleSearchChange}
            placeholder="Search by ID or Description"
			className={REVDASHCSS.repsearch}
          />
          <br />
          <label className={REVDASHCSS.label} htmlFor="statusFilter">
            Filter by Status
            <br />
          </label>
          <select className={REVDASHCSS.statusfilter} value={statusFilter} onChange={handleStatusChange}>
            <option value="All">All</option>
            <option value="Pending">Pending</option>
            <option value="Resolved">Resolved</option>
            <option value="Closed">Closed</option>
          </select>
        </form>

        <table className={REVDASHCSS.rephead}>
          <thead>
            <tr>
              <th className={REVDASHCSS.reportid}>Report ID</th>
              <th className={REVDASHCSS.description}>Description</th>
              <th className={REVDASHCSS.status}>Status</th>
            </tr>
          </thead>
        </table>

        <div className={REVDASHCSS.replist}>
          <table>
            <tbody>
              {report.length === 0 ? (
                <tr>
                  <td className={REVDASHCSS.td} colSpan="3">No reports found matching your criteria</td>
                </tr>
              ) : (
                report.map((rep) => (
                  <tr
                    key={rep.ReportID}
                    className={REVDASHCSS.clickablerow}
                    onClick={() => handleRowClick(rep.ReportID)}
                    style={{ cursor: "pointer" }} // Indicates clickable rows
                  >
                    <td className={`${REVDASHCSS.listreportid} ${REVDASHCSS.td}`}>{rep.ReportID}</td>
                    <td className={`${REVDASHCSS.listdescription} ${REVDASHCSS.td}`}>{rep.ReportDesc}</td>
					<td className={`${REVDASHCSS.liststatus} ${REVDASHCSS.td}`}>{rep.Status}</td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </main>

      <footer className={REVDASHCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={REVDASHCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
}

export default RelevantAuthority;
