// ban.js
import React, { useState } from 'react';
import BanCSS from './ban.module.css';
import logo from './Pictures/output-onlinepngtools.png';
import { supabase } from './createClient'; // Make sure createClient.js exports a supabase client
import { useAuth } from "./useAuth";
import {Link} from 'react-router-dom';

function Ban() {
	
	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};
	
  const [websiteURL, setWebsiteURL] = useState(''); // state to handle form input
  let ParID = 1;
  // Function to handle form submission
  const handleBanWebsite = async (event) => {
    event.preventDefault(); // Prevent form from refreshing page

    if (!websiteURL) {
      alert('Please enter a website URL');
      return;
    }

    try {
      // First, check if the website is already banned for the same parent
      const { data, error } = await supabase
        .from('BANNED_WEBSITE')
        .select('WebsiteURL')
        .eq('WebsiteURL', websiteURL) // Match the entered URL
        .eq('Parent', ParID); // Match the Parent (assuming Parent ID is 1)

      if (error) {
        throw new Error('Error checking existing website');
      }

      if (data.length > 0) {
        // If data is not empty, it means the website already exists for this parent
        alert('This website is already banned for this parent.');
      } else {
        // Insert the banned website URL into the Supabase table
        const { data: insertData, error: insertError } = await supabase
          .from('BANNED_WEBSITE')
          .insert([{ WebsiteURL: websiteURL, Parent: ParID }]); // Assuming "Parent" is a user ID or similar

        if (insertError) {
          console.error('Error banning website:', insertError.message);
          alert('Failed to ban website');
        } else {
          console.log('Website banned successfully:', insertData);
          alert('Website banned successfully');
          setWebsiteURL(''); // Reset input field after successful submission
        }
      }
    } catch (error) {
      console.error('Error checking website:', error);
      alert('An error occurred while checking the website.');
    }
  };

  return (
    <div>
      <header className={BanCSS.header}>
        <div className={BanCSS.logocontainer}>
          <Link className={BanCSS.Link} to='/ParentDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={BanCSS.Link} to='/ParentDashboard'><div className={BanCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={BanCSS.nav}>
          <ul className={BanCSS.pad}>
            <li><Link className={BanCSS.Link} to='/Plugin'>Plugin</Link></li>
			<li><Link className={BanCSS.Link} to='/ReportSystem'>Reporting System</Link></li>
            <li><button className={BanCSS.buttons} onClick={handleLogout}>Logout</button></li>
          </ul>
        </nav>
      </header>

      <main>
        <div className={BanCSS.ban}>
          <h1>Ban Website</h1>
          <form onSubmit={handleBanWebsite}>
            <label className={BanCSS.label} htmlFor="web">
              Enter the URL of the website to ban
              <br />
            </label>
            <input
              type="text"
              id="web"
              name="web"
              className={BanCSS.inputweb}
              placeholder="Input URL of website"
              value={websiteURL}
              onChange={(e) => setWebsiteURL(e.target.value)} // Update state with form input
              required
            />
            <br />
            <button className={BanCSS.button} type="submit">Ban Website</button>
          </form>
        </div>
      </main>

      <footer className={BanCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={BanCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default Ban;
