import React from 'react';
import Child from './ChildDashboard.module.css';
import logo from './Pictures/output-onlinepngtools.png';
import {Link} from 'react-router-dom';
import { useAuth } from "./useAuth";

const ChildDashboard = () => {
	
	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};
	
  return (
    <div>
      <header className={Child.header}>
        <div className={Child.logocontainer}>
          <Link className={Child.Link} to='/ChildDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={Child.Link} to='/ChildDashboard'><div className={Child.logotext}>ChildWatch Secure</div></Link>
        </div>

        <nav className={Child.nav}>
          <ul className={Child.pad}>
            <li>
              <Link to='/InformationPage'>Educational Materials</Link>
            </li>
            <li>
              <Link to='/Plugin'>Plugin</Link>
            </li>
            <li>
              <Link to='/ReportSystem'>Reporting System</Link>
            </li>
            <li>
              <button className={Child.buttons} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>

      <main>
        <img
          className={Child.imagefamily}
          src="Pictures/family.jpg"
          alt="Family"
        />
        <h1 className={Child.title}>Welcome to The Children's Dashboard</h1>

        <div className={Child.buttoncontainer}>
          <button
            className={`${Child.topbutton} ${Child.button}`} onClick={() => (window.location.href = '/Plugin')}>Download Plugin</button>
          <button className={`${Child.topbutton} ${Child.button}`} onClick={() => (window.location.href = '/ChildViewBan')}>View Banned Websites</button>
        </div>
      </main>

      <footer className={Child.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={Child.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default ChildDashboard;
