import React from 'react';
import HomepageCSS from './Homepage.module.css';
import logo from './Pictures/output-onlinepngtools.png';
import {Link} from 'react-router-dom';

const Homepage = () => {
  return (
    <div>
      <header className={HomepageCSS.headerhome}>
        <div className={HomepageCSS.logocontainer}>
          <Link to="/">
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: '50px', height: '50px' }}
            />
          </Link>
          <Link className={HomepageCSS.Link} to="/"><div className={HomepageCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={HomepageCSS.nav}>
          <ul className={HomepageCSS.pad}>
            <li>
              <Link to="/ReportSystem">Reporting System</Link>
            </li>
            <li>
              <Link to='/Login'><button className={HomepageCSS.buttons}>Login</button></Link>
            </li>
          </ul>
        </nav>
      </header>

      <div className={HomepageCSS.content}>
        <div className={HomepageCSS.introsection}>
          <h1 className={HomepageCSS.texttitle}>Welcome to ChildWatch Secure</h1>
          <h4 className={HomepageCSS.text}>
            Awareness and Action Against Online Abuse for a Safer Digital World
          </h4>
            <Link className={HomepageCSS.Link} to="/InformationPage"><button className={HomepageCSS.buttonlearnmore}>Learn More</button></Link>
        </div>

        <table className={HomepageCSS.centertable}>
          <tbody className={HomepageCSS.bodyhome}>
            <tr>
              <td>
                <h4>Sign-up to Access Services</h4>
                <Link className={HomepageCSS.Link} to="/SignUp"><button
                  className={HomepageCSS.buttonlearnmore}>Sign-Up</button></Link>
              </td>
              <td>
                <h4>LogIn if you already have an account</h4>
                <Link className={HomepageCSS.Link} to="/Login"><button
                  className={HomepageCSS.buttonlearnmore}>Login</button></Link>
              </td>
            </tr>
          </tbody>
        </table>

        <div className={HomepageCSS.introsection}>
          <h4 className={HomepageCSS.text}>Report an Incident</h4>
          <Link className={HomepageCSS.Link} to="/ReportSystem"><button className={HomepageCSS.buttonlearnmore}>Report Now</button></Link>
        </div>
      </div>

      <h2 className={HomepageCSS.aboutustitle}>About Us</h2>
      <p className={HomepageCSS.aboutustext}>
        Programmers:
        <br />
        Vicente G. Reambonanza III
        <br />
        Rain Ancheta
        <br />
        Jan Carlos Inoferio
        <br />
        <br />
        The project focuses on the development and implementation of an Online
        Sexual Abuse Awareness, Reporting, and Recovery System, a software
        solution designed to spread awareness of online sexual abuse, and
        provide two services, a reporting system and a recovery aid protocol for
        online sexual abuse victims.
        <br />
        <br />
        In the present time, online sexual abuse is at an all-time high, and
        local authorities tend to not understand or have proper knowledge and
        equipment to combat this. The Online Sexual Abuse Awareness, Reporting,
        and Recovery System aims to spread awareness and prevention for the
        masses, have a system in order for both the victims and authorities in
        capturing the offenders, and give the users the choice to seek help and
        start recovery.
        <br />
        <br />
        The primary objective of this project is to present an easy-access and
        user-friendly web application that would enable the prevention, action,
        and recovery against online sexual abuse for victims, key witnesses, or
        concerned citizens.
        <br />
        <br />
        The Online Sexual Abuse Awareness, Reporting, and Recovery System
        targets local cities such as Dumaguete City as this is a Local Online
        Sexual Abuse Awareness, Reporting, and Recovery System but can be
        expanded to cover other cities simultaneously. Our aim is to create and
        implement the Online Sexual Abuse Awareness, Reporting, and Recovery
        System website on or before the deadline, which is on December 13, 2024,
        which would approximately be 3 months and 6 days.
      </p>

      <footer className={HomepageCSS.footerhome}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={HomepageCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default Homepage;
