// Import necessary dependencies
import React, { useState, useEffect } from "react";
import ParentCSS from "./ParentViewBan.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import { supabase } from "./createClient";
import { useAuth } from "./useAuth";
import {Link} from 'react-router-dom';

function ParentViewBan() {
	
	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};
	
  const [SITE, setSITE] = useState([]);
  const [searchQuery, setSearchQuery] = useState(""); // State to store the search input
  const id = 1;
  // Fetch all sites initially
  useEffect(() => {
    fetchSITE();
  }, []);

  async function fetchSITE() {
    const { data } = await supabase.from("BANNED_WEBSITE").select("*").eq("Parent", id);
    setSITE(data);
    console.log(data);
  }

  useEffect(() => {
    if (searchQuery === "") {
      fetchSITE();
    } else {
      search();
    }
  }, [searchQuery]);

  async function search() {
    const { data } = await supabase.from("BANNED_WEBSITE").select("*").ilike("WebsiteURL", `%${searchQuery}%`);
    setSITE(data);
    console.log(data);
  }

  const handleInputChange = (event) => {
    setSearchQuery(event.target.value);
  };

  // Function to handle unbanning a website with confirmation
  async function handleUnban(id) {
    const confirmation = window.confirm("Are you sure you want to unban this website?");
    if (!confirmation) {
      return; // Exit if the user cancels the confirmation
    }

    const { error } = await supabase
      .from("BANNED_WEBSITE")
      .delete()
      .eq("BanID", id);

    if (error) {
      console.error("Error unbanning website:", error);
    } else {
      console.log("Website unbanned successfully");
      // Update state to remove the deleted site
      setSITE(SITE.filter((site) => site.BanID !== id));
    }
  }

  return (
    <div>
      <header className={ParentCSS.header}>
        <div className={ParentCSS.logocontainer}>
          <Link className={ParentCSS.Link} to='/ParentDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={ParentCSS.Link} to='/ParentDashboard'><div className={ParentCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={ParentCSS.nav}>
          <ul className={ParentCSS.pad}>
            <li><Link className={ParentCSS.Link} to='/Plugin'>Plugin</Link></li>
			<li><Link className={ParentCSS.Link} to='/ReportSystem'>Reporting System</Link></li>
            <li><button className={ParentCSS.buttons} onClick={handleLogout}>Logout</button></li>
          </ul>
        </nav>
      </header>

      <div className={ParentCSS.unban}>
        <h1>Banned Websites</h1>
        <form>
          <label className="label" htmlFor="web">
            Enter URL website<br />
          </label>
          <input
            type="text"
            id="web"
            name="web"
            className={ParentCSS.inputweb}
            value={searchQuery}
            onChange={handleInputChange}
            required
          />
          <br />
        </form>
        <div className={ParentCSS.unbanlist}>
          {SITE.map((site) => (
            <div className={ParentCSS.unbanline} key={site.BanID}>
              <p>
                {site.WebsiteURL}
                <button className={ParentCSS.button} onClick={() => handleUnban(site.BanID)}>Unban Website</button>
              </p>
            </div>
          ))}
        </div>
      </div>
      <footer className={ParentCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={ParentCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>

    </div>
  );
}

export default ParentViewBan;
