import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import HOMEpage from './components/Homepage';
import LOGIN from './components/Login';
import SignUP from './components/SignUp';
import INFOpage from './components/InformationPage';
import PLUGIN from './components/PlugIn'
import BAN from './components/ban'
import ParentView from './components/ParentViewBan';
import ParentDASH from './components/ParentDashboard';
import ADDCHILD from './components/AddDesignatedChild';
import REPORTSYS from './components/ReportSystem';
import RELEVANTAUTH from './components/RelevantAuthority';
import ReportDetailsPage from "./components/ReportDetails"; 
import CHILDDASH from './components/ChildDashboard';
import AddChild from './components/AddDesignatedChild';
import CHILDBAN from './components/ChildViewBan';

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<HOMEpage />} />
        <Route path="/Login" element={<LOGIN />} />
		<Route path="/SignUp" element={<SignUP />} />
		<Route path="/InformationPage" element={<INFOpage />} />
		<Route path="/PlugIn" element={<PLUGIN />} />
		<Route path="/ban" element={<BAN />} />
		<Route path="/ParentViewBan" element={<ParentView />} />
		<Route path="/ParentDashboard" element={<ParentDASH />} />
		<Route path="/ReportSystem" element={<REPORTSYS />} />
		<Route path="/RelevantAuthority" element={<RELEVANTAUTH />} />
		<Route path="/report/:reportId" element={<ReportDetailsPage />} />
		<Route path="/ChildDashboard" element={<CHILDDASH />} />
		<Route path="/AddDesignatedChild" element={<AddChild />} />
		<Route path="/ChildViewBan" element={<CHILDBAN />} />
      </Routes>
    </Router>
  );
}

export default App;