import React from "react";
import ParentDashCSS from "./ParentDashboard.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import {Link} from 'react-router-dom';
import { useAuth } from "./useAuth";

const ParentDashboard = () => {
	useAuth();
	
	const handleLogout = () => {
	  localStorage.removeItem("user");
	  alert("You have been logged out.");
	  window.location.href = "/";
	};

  return (
    <div>
      <header className={ParentDashCSS.header}>
        <div className={ParentDashCSS.logocontainer}>
          <Link className={ParentDashCSS.Link} to='/ParentDashboard'>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={ParentDashCSS.Link} to='/ParentDashboard'><div className={ParentDashCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={ParentDashCSS.nav}>
          <ul className={ParentDashCSS.pad}>
            <li>
              <Link to='/Plugin'>Plugin</Link>
            </li>
            <li>
              <Link to='/ReportSystem'>Reporting System</Link>
            </li>
            <li>
              <button className={ParentDashCSS.buttons} onClick={handleLogout}>Logout</button>
            </li>
          </ul>
        </nav>
      </header>

      <main>
        <img
          className={ParentDashCSS.imagefamily}
          src="Pictures/family.jpg"
          alt="Family"
        />
        <h1 className={ParentDashCSS.title}>Welcome to Your Parent Dashboard</h1>
        <table className={ParentDashCSS.tablearrangement}>
          <tbody>
            <tr>
              <td>
                <Link to='/ban'><button className={`${ParentDashCSS.button} ${ParentDashCSS.topbutton}`}>Ban Website</button></Link>
              </td>
              <td>
                <Link to='/ParentViewBan'><button className={`${ParentDashCSS.button} ${ParentDashCSS.topbutton}`}>View Banned Websites</button></Link>
              </td>
              <td>
                <Link to='/AddDesignatedChild'><button className={`${ParentDashCSS.button} ${ParentDashCSS.topbutton}`}>Create a Child Account</button></Link>
              </td>
            </tr>
          </tbody>
        </table>
      </main>

      <footer className={ParentDashCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={ParentDashCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default ParentDashboard;
