import React, { useState } from "react";
import SignUpCSS from "./Signup.module.css";
import logo from './Pictures/output-onlinepngtools.png';
import account from './Pictures/new acc logo.png';
import { Link, useNavigate } from 'react-router-dom';
import { supabase } from "./createClient";

const SignUp = () => {
  const navigate = useNavigate();	
  const [accountType, setAccountType] = useState("Parent");
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    gender: "male",
    contactNumber: "",
    department: "",
    email: "",
    password: "",
  });

  const handleAccountTypeChange = (event) => {
    setAccountType(event.target.value);
  };

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (event) => {
  event.preventDefault();

  try {
    let insertData;
    let newId;

    const getMaxId = async (table, column) => {
      const { data, error } = await supabase
        .from(table)           
        .select(column)        
        .order(column, { ascending: false }) // Order by the column in descending order
        .limit(1);              
    
      if (error) throw error;
    
      return data.length > 0 ? data[0][column] : 0;  
    };
    
    const maxParentId = await getMaxId('PARENT', 'ParentID');
    const maxRAId = await getMaxId('RELEVANT_AUTHORITY', 'RAID');

    if (accountType === "Relevant Authority") {

      newId = maxRAId + 1;
      insertData = {
        RAID: newId,
        RAFname: formData.firstName,
        RALname: formData.lastName,
        RAGender: formData.gender,
        Department: formData.department,
        RAemail: formData.email,
        RAPhoneNum: formData.contactNumber,
        RAPass: formData.password,
      };

      const { data, error } = await supabase
        .from("RELEVANT_AUTHORITY")
        .insert([insertData]);

      if (error) throw error;

    } else {
      newId = maxParentId + 1;
      insertData = {
        ParentID: newId,
        ParentFname: formData.firstName,
        ParentLname: formData.lastName,
        ParentGender: formData.gender,
        ParentPhonNum: formData.contactNumber,
        ParentEmail: formData.email,
        ParentPass: formData.password,
      };

      const { data, error } = await supabase
        .from("PARENT")
        .insert([insertData]);


      if (error) throw error;
    }
	
	 alert("Account created successfully!");
     navigate('/Login');

  } catch (error) {
    console.error("Error inserting data:", error);
    alert("Error creating account. Please try again.");
  }
};

  return (
    <div>
      <header className={SignUpCSS.header}>
        <div className={SignUpCSS.logocontainer}>
          <Link to="/">
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: '50px', height: '50px' }}
            />
          </Link>
          <Link className={SignUpCSS.Link} to="/"><div className={SignUpCSS.logotext}>ChildWatch Secure</div></Link>
        </div>
        <nav className={SignUpCSS.nav}>
          <ul className={SignUpCSS.pad}>
            <li>
              <Link to="/ReportSystem">Reporting System</Link>
            </li>
            <li>
              <button className={SignUpCSS.buttons} onClick={() => (window.location.href = '/Login')}>Login</button>
            </li>
          </ul>
        </nav>
      </header>

      {/* Account Information Section */}
      <table className={SignUpCSS.table}>
        <tbody>
          <tr>
            <td>
              <img
                src={account}
                alt="sample"
                className={SignUpCSS.acclogo}
              />
            </td>
            <td>
              <br />
              <br />
              <br />
              <h2>Create New Account</h2>
            </td>
          </tr>
        </tbody>
      </table>

      <div className={SignUpCSS.bigwords}>
        <h1 className={SignUpCSS.tablealign}>Account Information</h1>
        <h5 className={SignUpCSS.tablealign}>Fill in the details below</h5>
      </div>

      {/* Form Section */}
      <div className={SignUpCSS.container}>
        <label className={SignUpCSS.label} htmlFor="account-type">
          Account Type
        </label>
        <br />
        <select value={accountType} onChange={handleAccountTypeChange}>
          <option value="Relevant Authority">Relevant Authority</option>
          <option value="Parent">Parent</option>
        </select>
        <form onSubmit={handleSubmit}>
          {/* Textbox for First Name */}
          <label className={SignUpCSS.label} htmlFor="first-name">
            First Name:
          </label>
          <br />
          <input
            type="text"
            id="first-name"
            name="firstName"
            className={SignUpCSS.field}
            value={formData.firstName}
            onChange={handleChange}
            required
          />
          <br />

          {/* Textbox for Last Name */}
          <label className={SignUpCSS.label} htmlFor="last-name">
            Last Name:
          </label>
          <br />
          <input
            type="text"
            id="last-name"
            name="lastName"
            className={SignUpCSS.field}
            value={formData.lastName}
            onChange={handleChange}
            required
          />
          <br />

          {/* Gender Selection */}
          <label className={SignUpCSS.label} htmlFor="gender">
            Gender:
          </label>
          <div className={SignUpCSS.genderselection}>
            <div className={SignUpCSS.radiooptions}>
              <label className={SignUpCSS.radiooption}>
                <input
                  className={SignUpCSS.input}
                  type="radio"
                  name="gender"
                  value="male"
                  checked={formData.gender === "male"}
                  onChange={handleChange}
                />
                <span>Male</span>
              </label>
              <label className={SignUpCSS.radiooption}>
                <input
                  className={SignUpCSS.input}
                  type="radio"
                  name="gender"
                  value="female"
                  checked={formData.gender === "female"}
                  onChange={handleChange}
                />
                <span>Female</span>
              </label>
            </div>
          </div>
          <br />

          {/* Contact Number */}
          <label className={SignUpCSS.label} htmlFor="contact-number">
            Contact Number (optional):
          </label>
          <br />
          <input
            type="text"
            id="contact-number"
            name="contactNumber"
            className={SignUpCSS.field}
            value={formData.contactNumber}
            onChange={handleChange}
            required
          />
          <br />
		  
		  {/* Conditionally render the "Department" field if the account type is "Relevant Authority" */}
          {accountType === "Relevant Authority" && (
            <>
              <label className={SignUpCSS.label} htmlFor="department">
                Department:
              </label>
              <br />
              <input
                type="text"
                id="department"
                name="department"
                className={SignUpCSS.field}
                value={formData.department}
                onChange={handleChange}
                required
              />
              <br />
            </>
          )}

          {/* Email */}
          <label className={SignUpCSS.label} htmlFor="email">
            Email:
          </label>
          <br />
          <input
            type="text"
            id="email"
            name="email"
            className={SignUpCSS.field}
            value={formData.email}
            onChange={handleChange}
            required
          />
          <br />

          {/* Password */}
          <label className={SignUpCSS.label} htmlFor="password">
            Password:
          </label>
          <br />
          <input
            type="password"
            id="password"
            name="password"
            className={SignUpCSS.field}
            value={formData.password}
            onChange={handleChange}
            required
          />
          <br />

          {/* Submit Button */}
          <button className={SignUpCSS.button} type="submit">
            Create Account
          </button>
        </form>
      </div>

      {/* Footer Section */}
      <footer className={SignUpCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={SignUpCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default SignUp;