import React, { useState, useEffect } from "react";
import REPCSS from "./ReportPage.module.css";
import logo from "./Pictures/output-onlinepngtools.png";
import cyber from "./Pictures/CyberSafe-ReportSysImg.jpg";
import { supabase } from "./createClient";
import { Link, useNavigate } from 'react-router-dom';

const ReportSystem = () => {
  const [location, setLocation] = useState("");
  const [contact, setContact] = useState("");
  const [message, setMessage] = useState("");
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [successMessage, setSuccessMessage] = useState("");
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const navigate = useNavigate();
  const [userAccountType, setUserAccountType] = useState(null);

	useEffect(() => {
	  const user = JSON.parse(localStorage.getItem("user"));
	  if (user) {
		setIsLoggedIn(true);
		setUserAccountType(user.accountType); // Save the account type in state
	  } else {
		setIsLoggedIn(false);
	  }
	}, []);


	 const handleLogout = () => {
		localStorage.removeItem("user");
		setIsLoggedIn(false); 
		navigate("/"); 
	 };

  const handleFileUpload = async (file) => {
    const fileName = `${Date.now()}_${file.name}`;
    
    const { data, error } = await supabase.storage
      .from("pdf-reports") 
      .upload(`reports/${fileName}`, file); 

    if (error) {
      throw new Error("Failed to upload file: " + error.message);
    }

    return data.path;
  };


  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);

    const isConfirmed = window.confirm(
      "Are you sure you want to submit this report? Once submitted, it cannot be changed."
    );

    if (!isConfirmed) {
      setLoading(false);
      return;
    }

    try {
      const now = new Date();
      const formattedDate = `${now.getFullYear()}-${String(now.getMonth() + 1).padStart(2, "0")}-${String(now.getDate()).padStart(2, "0")}`;

      let filePath = null;

      if (file) {
        filePath = await handleFileUpload(file);
      }

      const { error } = await supabase.from("REPORT").insert({
        Location: location,
        Contact: contact || null,
        ReportDesc: message,
        ReportFile: filePath || null, 
        DateReported: formattedDate,
        Status: "Pending",
      });

      if (error) {
        throw error;
      }

      setLocation("");
      setContact("");
      setMessage("");
      setFile(null);
      setSuccessMessage("Your report has been submitted successfully!");

      setTimeout(() => {
        setSuccessMessage("");
      }, 5000);
    } catch (err) {
      alert("Error submitting report: " + err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <header className={REPCSS.header}>
        <div className={REPCSS.logocontainer}>
          <Link to={isLoggedIn ? userAccountType == "Parent" ? "/ParentDashboard" : userAccountType == "Child" ? "/ChildDashboard" : "/" : "/"}>
            <img
              src={logo}
              alt="ChildWatch Secure Logo"
              style={{ width: "50px", height: "50px" }}
            />
          </Link>
          <Link className={REPCSS.Link} to={isLoggedIn ? userAccountType == "Parent" ? "/ParentDashboard" : userAccountType == "Child" ? "/ChildDashboard" : "/" : "/"}>
            <div className={REPCSS.logotext}>ChildWatch Secure</div>
          </Link>
        </div>
		<nav className={REPCSS.nav}>
		  <ul className={REPCSS.pad}>
			<li>
			   {userAccountType === "Child" && (
					<Link className={REPCSS.pluginLink} to="/InformationPage">
					  <span className={REPCSS.pluginText}>Educational Materials</span>
					</Link>)} &nbsp;&nbsp;&nbsp;
			  {isLoggedIn && (
				<Link className={REPCSS.pluginLink} to="/PlugIn">
				  <span className={REPCSS.pluginText}>Plugin</span>
				</Link>
			  )}&nbsp;&nbsp;&nbsp;&nbsp;
			  <Link className={REPCSS.Link} to="/ReportSystem">
				Reporting System
			  </Link>
			</li>
			<li>
			  {isLoggedIn ? (
				<button className={REPCSS.buttons} onClick={handleLogout}>
				  Logout
				</button>
			  ) : (
				<button className={REPCSS.buttons} onClick={() => (window.location.href = '/Login')}>Login</button>
			  )}
			</li>
		  </ul>
		</nav>

      </header>

      <table className={`${REPCSS.spacetop} ${REPCSS.table}`}>
        <tbody>
          <tr>
            <td className={`${REPCSS.tableText} ${REPCSS.td}`}>
              <h2>
                Online Sexual Abuse Awareness, Reporting, and Recovery System
              </h2>
              <p className={REPCSS.normaltext}>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                Creating a safe environment for reporting incidents
              </p>
            </td>
            <td className={REPCSS.td}>
              <img
                className={REPCSS.tableImage}
                src={cyber}
                alt="Description of Image"
              />
            </td>
          </tr>
        </tbody>
      </table>

      <br />
      <br />
      <br />

      <table className={REPCSS.table}>
        <tbody>
          <tr>
            <td className={`${REPCSS.tableText} ${REPCSS.td}`}>
              <h1>Report an incident</h1>
            </td>
            <td className={REPCSS.td}>
              <form className={REPCSS.form} onSubmit={handleSubmit} encType="multipart/form-data">
                <label className={REPCSS.label} htmlFor="location">
                  Location of Incident:
                </label>
                <input
                  type="text"
                  id="location"
                  name="location"
                  value={location}
                  onChange={(e) => setLocation(e.target.value)}
                  required
                />
                <br />

                <label className={REPCSS.label} htmlFor="contact">
                  Contact Number (optional):
                </label>
                <input
                  type="text"
                  id="contact"
                  name="contact"
                  value={contact}
                  onChange={(e) => setContact(e.target.value)}
                  className={REPCSS.textInput}
                />
                <br />

                <label className={REPCSS.label} htmlFor="message">
                  Incident Description:
                </label>
                <textarea
                  id="message"
                  name="message"
                  rows="4"
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  required
                  className={REPCSS.textareas}
                />
                <br />

                <label className={REPCSS.label} htmlFor="image">
                  PDF file upload (images, details, etc. should be inside the
                  file):
                </label>
                <br />
                <input
                  type="file"
                  id="image"
                  name="image"
                  accept="application/pdf"
                  onChange={(e) => setFile(e.target.files[0])}
                  className={REPCSS.fileInput}
                />
                <br />

                <button className={REPCSS.button} type="submit" disabled={loading}>
                  {loading ? "Submitting..." : "Submit Report"}
                </button>
              </form>
            </td>
          </tr>
        </tbody>
      </table>

      {successMessage && <div className={REPCSS.successMessage}>{successMessage}</div>}

      <br />
      <br />
      <br />

      <footer className={REPCSS.footer}>
        <p>© 2024 ChildWatch Secure. All Rights Reserved.</p>
        <div className={REPCSS.footerlinks}>
          <a href="#">Privacy Policy</a>
          <a href="#">Terms of Use</a>
        </div>
      </footer>
    </div>
  );
};

export default ReportSystem;